import axios from 'axios';
import { setLoading, setTutors, addTutor, updateTutor, removeTutor, setError } from './tutorSlice';

const API_URL = process.env.REACT_APP_API_URL || 'http://localhost:5000/api/tutors';

export const fetchTutors = () => async (dispatch) => {
  try {
    dispatch(setLoading(true));
    const response = await axios.get(API_URL);
    dispatch(setTutors(response.data));
  } catch (error) {
    dispatch(setError(error.message));
  }
};

export const fetchTutorById = (id) => async () => {
  try {
    const response = await axios.get(`${API_URL}/${id}`);
    return response.data;
  } catch (error) {
    throw error;
  }
};

export const createTutor = (tutorData) => async (dispatch) => {
  try {
    const response = await axios.post(API_URL, tutorData);
    dispatch(addTutor(response.data));
    return response.data;
  } catch (error) {
    dispatch(setError(error.message));
    throw error;
  }
};

export const editTutor = (id, tutorData) => async (dispatch) => {
  try {
    const response = await axios.put(`${API_URL}/${id}`, tutorData);
    dispatch(updateTutor(response.data));
    return response.data;
  } catch (error) {
    dispatch(setError(error.message));
    throw error;
  }
};

export const deleteTutor = (id) => async (dispatch) => {
  try {
    await axios.delete(`${API_URL}/${id}`);
    dispatch(removeTutor(id));
  } catch (error) {
    dispatch(setError(error.message));
    throw error;
  }
};