import { createSlice } from '@reduxjs/toolkit';

const tutorSlice = createSlice({
  name: 'tutors',
  initialState: {
    tutors: [],
    loading: false,
    error: null
  },
  reducers: {
    setLoading: (state, action) => {
      state.loading = action.payload;
    },
    setTutors: (state, action) => {
      state.tutors = action.payload;
      state.loading = false;
    },
    addTutor: (state, action) => {
      state.tutors.push(action.payload);
    },
    updateTutor: (state, action) => {
      const index = state.tutors.findIndex(t => t._id === action.payload._id);
      if (index !== -1) {
        state.tutors[index] = action.payload;
      }
    },
    removeTutor: (state, action) => {
      state.tutors = state.tutors.filter(t => t._id !== action.payload);
    },
    setError: (state, action) => {
      state.error = action.payload;
      state.loading = false;
    }
  }
});

export const { setLoading, setTutors, addTutor, updateTutor, removeTutor, setError } = tutorSlice.actions;
export default tutorSlice.reducer;