import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { fetchTutors, deleteTutor } from '../redux/tutorActions';

function TutorList() {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { tutors, loading } = useSelector(state => state.tutors);

  useEffect(() => {
    dispatch(fetchTutors());
  }, [dispatch]);

  const handleDelete = (id) => {
    if (window.confirm('Are you sure you want to delete this tutor?')) {
      dispatch(deleteTutor(id));
    }
  };

  if (loading) {
    return <div className="loading">Loading...</div>;
  }

  return (
    <div className="tutor-list">
      <h2>Available Tutors</h2>
      {tutors.length === 0 ? (
        <p>No tutors available. Add one to get started!</p>
      ) : (
        <div className="tutor-grid">
          {tutors.map(tutor => (
            <div key={tutor._id} className="tutor-card">
              <h3>{tutor.name}</h3>
              <p><strong>Subject:</strong> {tutor.subject}</p>
              <p><strong>Experience:</strong> {tutor.experience} years</p>
              <p><strong>Rate:</strong> ${tutor.rate}/hour</p>
              <div className="tutor-actions">
                <button onClick={() => navigate(`/tutor/${tutor._id}`)} className="btn-view">
                  View
                </button>
                <button onClick={() => navigate(`/edit/${tutor._id}`)} className="btn-edit">
                  Edit
                </button>
                <button onClick={() => handleDelete(tutor._id)} className="btn-delete">
                  Delete
                </button>
              </div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

export default TutorList;