import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate, useParams } from 'react-router-dom';
import { createTutor, editTutor, fetchTutorById } from '../redux/tutorActions';

function TutorForm() {
  const { id } = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    name: '',
    subject: '',
    experience: '',
    rate: '',
    email: ''
  });

  useEffect(() => {
    if (id) {
      dispatch(fetchTutorById(id)).then(data => {
        setFormData({
          name: data.name,
          subject: data.subject,
          experience: data.experience,
          rate: data.rate,
          email: data.email
        });
      });
    }
  }, [id, dispatch]);

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (id) {
        await dispatch(editTutor(id, formData));
      } else {
        await dispatch(createTutor(formData));
      }
      navigate('/');
    } catch (error) {
      alert('Error saving tutor');
    }
  };

  return (
    <div className="tutor-form">
      <h2>{id ? 'Edit Tutor' : 'Add New Tutor'}</h2>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label>Name:</label>
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label>Subject:</label>
          <input
            type="text"
            name="subject"
            value={formData.subject}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label>Experience (years):</label>
          <input
            type="number"
            name="experience"
            value={formData.experience}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label>Rate ($/hour):</label>
          <input
            type="number"
            name="rate"
            value={formData.rate}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-group">
          <label>Email:</label>
          <input
            type="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
            required
          />
        </div>
        <div className="form-actions">
          <button type="submit" className="btn-submit">
            {id ? 'Update' : 'Add'} Tutor
          </button>
          <button type="button" onClick={() => navigate('/')} className="btn-cancel">
            Cancel
          </button>
        </div>
      </form>
    </div>
  );
}

export default TutorForm;