import React from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import TutorList from './components/TutorList';
import TutorForm from './components/TutorForm';
import TutorDetail from './components/TutorDetail';

function App() {
  return (
    <Router>
      <div className="app">
        <nav className="navbar">
          <h1>Tutor Booking App</h1>
          <div className="nav-links">
            <Link to="/">Home</Link>
            <Link to="/add">Add Tutor</Link>
          </div>
        </nav>
        <div className="container">
          <Routes>
            <Route path="/" element={<TutorList />} />
            <Route path="/add" element={<TutorForm />} />
            <Route path="/edit/:id" element={<TutorForm />} />
            <Route path="/tutor/:id" element={<TutorDetail />} />
          </Routes>
        </div>
      </div>
    </Router>
  );
}

export default App;